<template>
  <!--  -->
  <div class="type-nav">
    <div class="container">
      <h2 class="all" @mouseenter="isShow = true" @mouseout.once="isShow = false"></h2>
      <nav class="nav">
        <a href="javascript:;"></a>
        <a href="javascript:;"></a>
        <a href="javascript:;">Zek</a>
        <a href="javascript:;"></a>
        <a href="javascript:;"></a>
        <a href="javascript:;"></a>
        <a href="javascript:;"></a>
        <a href="javascript:;"></a>
      </nav>
      <div class="sort" v-show="isShow">
        <div class="all-sort-list2" @click="toSearch">
          <div class="item" v-for="c1 in categoryList" :key="c1.categoryId">
            <h3>
              <!-- 1router-view +  -->
              <!-- <router-link :to="{name:'search',query:{category1Id:c1.categoryId,categoryName:c1.categoryName}}">{{c1.categoryName}}</router-link> -->

              <!-- 2a + aclick -->
              <!-- <a href="javascript:;" @click="$router.push({name:'search',query:{category1Id:c1.categoryId,categoryName:c1.categoryName}})">{{c1.categoryName}}</a> -->
             
             
              <!-- 3aadata-xxx-->
              <a href="javascript:;" 
              :data-category1Id="c1.categoryId"
              :data-categoryName="c1.categoryName"
              >{{c1.categoryName}}</a>

            </h3>
            <div class="item-list clearfix">
              <div class="subitem">
                <dl class="fore" v-for="c2 in c1.categoryChild" :key="c2.categoryId">
                  <dt>
                    <!-- <a href="javascript:;" @click="$router.push({name:'search',query:{category2Id:c2.categoryId,categoryName:c2.categoryName}})">{{c2.categoryName}}</a> -->
                    <!-- <router-link :to="{name:'search',query:{category2Id:c2.category2Id,categoryName:c2.categoryName}}">{{c2.categoryName}}</router-link> -->
                    <a href="javascript:;" 
                    :data-category2Id="c2.categoryId"
                    :data-categoryName="c2.categoryName"
                    >{{c2.categoryName}}</a>
                  </dt>
                  <dd>
                    <em v-for="c3 in c2.categoryChild" :key="c3.categoryId">
                      <!-- <a href="javascript:;" @click="$router.push({name:'search',query:{categoryId:c3.categoryId,categoryName:c3.categoryName}})">{{c3.categoryName}}</a> -->
                      <!-- <router-link :to="{name:'search',query:{category3Id:c3.categoryId,categoryName:c3.categoryName}}">{{c3.categoryName}}</router-link> -->
                      <a href="javascript:;" 
                      :data-category3Id="c3.categoryId"
                      :data-categoryName="c3.categoryName"
                      >{{c3.categoryName}}</a>
                    </em>
                  </dd>
                </dl>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name:"TypeNav",
  data(){
    return {
      isShow:true
    }
  },
  mounted(){

    // @/store/home/index.jsactionsvuexstate
    // this.$store.dispatch("getCategoryList");

    if(this.$route.path!="/home"){
      this.isShow = false;
    }
  },
  // vuexvue
  computed:{
    // mapState
    // 
    // 1state
    // 2state
    // ...mapState(["categoryList"])
    // categoryListstate.categoryListstate.home.categoryList
    ...mapState({
      // state.home.categoryList
      categoryList:state=>state.home.categoryList
    })
  },

  methods:{

    // searchcategoryIdcategoryName
    toSearch(evt){

      // 
      let target = evt.target;

      // dataset
      let targDataSet = target.dataset;

      // dataset
      let {category1id,category2id,category3id,categoryname} = targDataSet;  

      // categorynamea
      if(categoryname){

        // this.$router.push()
        let location = {name:"search"};
      
        // categoryName
        let query = {categoryName:categoryname}

        // categoryId
        if(category1id){
          query.category1Id = category1id;
        }else if(category2id){
          query.category2Id = category2id;
        }else{
          query.category3Id = category3id;
        }

        // query
        location.query = query;

        // params
        if(this.$route.params){
          location.params = this.$route.params;
        }

        this.$router.push(location);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.type-nav {
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background: #fafafa;
      z-index: 999;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 615px;
                  padding: 3px 0 0;
                  overflow: hidden;
                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    a{
                      margin-left: 5px;
                      padding-left: 3px;
                      border-left: 1px solid #ccc;
                    }
                  }
                }
              }
            }
          }

          &:hover {
            background-color: #aaa;
            .item-list {
              display: block;
            }
          }
        }
      }
    }
  }
}
</style>