<template>
  <div class="pay_wrap">
    <h3 style="margin: 10px 0">
      ~~
    </h3>
    <p>
      <span style="color: #ea4a36">4</span
      >{{ orderNum }}
    </p>
    <p style="color: #ea4a36; font-size: 20px">{{ payInfo.money }}</p>
    <button class="submit" @click="pay"></button>
    <div class="explanation">
      <h2></h2>
      <ul>
        <li>1.</li>
        <li>2.</li>
        <li>3.</li>
      </ul>
      <h2></h2>
      <ul>
        <li>14528</li>
        <li>*****</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Pay",
  beforeMount() {
    this.orderNum = this.$route.query.orderNum;
  },
  mounted() {
    this.getPayInfo();
  },
  data() {
    return {
      orderNum: "",
      payInfo: {},
      payStatus:0,
    };
  },
  methods: {
    // 
    async getPayInfo() {
      const result = await this.$API.reqPayInfo();
      if (result.code == 200) {
        this.payInfo = result.data;
      } else {
        alert("");
      }
    },

    // 
    pay() {
        // 
        if(this.payInfo){
          // 
          this.$alert(`<img src=${this.payInfo.codeUrl} style="width:150px;">`, "", {
              dangerouslyUseHTMLString: true,
              showClose:false,
              center:true,
              showConfirmButton:true,
              showCancelButton:true,
              confirmButtonText:"",
              cancelButtonText:"",
              // beforeClose
              beforeClose:(action,instance,done)=>{
                // action'confirm''cancel''close'
                if(action === "confirm"){
                  // 
                  if(!this.payStatus){
                    // 
                    // 1.
                    this.$message.error("");

                  }
                }else if(action === "cancel"){
                  // 1.
                  this.$message.warning("400-1813562");
                  // 2.
                  clearInterval(this.timer);
                  this.timer = null;
                  // 3.
                  done();
                }
              }
          }).then(()=>{}).catch(()=>{})//.then.catchmessageBox
          // beforeClose

          // 
          if(!this.timer){
            this.timer = setInterval(async ()=>{
              let result = await this.$API.reqPaySatus(this.orderNum);
              if(result.code==200){
                // 1
                this.payStatus = 200;

                // 2
                this.$message.success("");

                // 3
                clearInterval(this.timer);  //clearInterval
                this.timer = null;          //id

                // 4.
                this.$msgbox.close();       //
                this.$router.push("/paysuccess");    

              }
            },2000);
          }

        }else{
          this.$alert("");
        }
    },
  },
};
</script>

<style scoped>
.pay_wrap {
  margin: 20px auto;
  width: 90%;
}
.pay_wrap p {
  height: 30px;
  line-height: 30px;
}
.explanation {
  margin-top: 10px;
  border: 2px solid #ea4a36;
  padding: 10px;
}
.explanation li {
  line-height: 20px;
  height: 20px;
  padding: 5px 0;
}
.explanation h2 {
  color: #ea4a36;
}
.submit{
  border:none;
  background-color: #ea4a36;
  color: #fff;
  width: 80px;
  height: 40px;
  line-height: 30px;
  padding:5px;
  cursor: pointer;
  margin: 10px 0;
}
.submit:hover{
  background-color: #cf5140;
}
</style>